// pages/LoginPage/ui/LoginPage.jsx
import React, { useState } from "react";
import { Card, Form, Input, Button, Typography, Alert } from "antd";
import { useNavigate } from "react-router-dom";
import { loginFx } from "../../../features/auth/model/login";
import { setSession } from "../../../features/auth/model/session";

const { Title } = Typography;

export const LoginPage = () => {
    const navigate = useNavigate();
    const [error, setError] = useState("");

    const onFinish = (values) => {
        const { login, password } = values;
        const user = loginFx({ login, password });
        if (!user) {
            setError("   ");
        } else {
            setError("");
            //   localStorage
            setSession(user);
            //    (Dashboard)   
            navigate("/");
        }
    };

    return (
        <div style={{ display: "flex", justifyContent: "center", padding: "50px" }}>
            <Card style={{ width: 400 }}>
                <Title level={2} style={{ textAlign: "center" }}>
                      
                </Title>
                {error && <Alert message={error} type="error" style={{ marginBottom: 16 }} />}
                <Form layout="vertical" onFinish={onFinish}>
                    <Form.Item
                        label=""
                        name="login"
                        rules={[{ required: true, message: " " }]}
                    >
                        <Input placeholder="student  teacher" />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="password"
                        rules={[{ required: true, message: " " }]}
                    >
                        <Input.Password placeholder="12345  teacher" />
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" block>
                            
                        </Button>
                    </Form.Item>
                </Form>
            </Card>
        </div>
    );
};
