import React from "react";
import { Layout, Menu, Breadcrumb } from "antd";
import {
    HomeOutlined,
    BookOutlined,
    CalendarOutlined,
    UserOutlined,
    PieChartOutlined,
} from "@ant-design/icons";
import { NavLink, Outlet, useLocation, useNavigate } from "react-router-dom";
import { getSession, clearSession } from "../../../features/auth/model/session";
import { breadcrumbNameMap } from "../../../shared/config/breadcrumbs";
import Logo from "../../../shared/ui/Logo/Logo";

const { Sider, Header, Content } = Layout;

export const AppLayout = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const user = getSession();

    React.useEffect(() => {
        if (!user) {
            navigate("/login");
        }
    }, [user, navigate]);

    //   ( )
    const selectedKey = `/${location.pathname.split("/")[1]}`;

    // ---   ---
    //     
    const pathSnippets = location.pathname.split("/").filter(Boolean);
    // : "/courses/123" -> ["courses", "123"]

    //   <Breadcrumb.Item>
    const extraBreadcrumbItems = pathSnippets.map((segment, index) => {
        const url = "/" + pathSnippets.slice(0, index + 1).join("/");
        // ,  segment="courses" url="/courses",
        //  segment="123" url="/courses/123"

        let name = breadcrumbNameMap[url];
        if (!name && index > 0 && pathSnippets[0] === "courses") {
            //    "courses",     (  ID)
            //  " " +  ID ( )
            name = `  (ID: ${segment})`;
        }

        //     name,    segment (fallback)
        if (!name) {
            name = segment;
        }

        return (
            <Breadcrumb.Item key={url}>
                <NavLink to={url}>{name}</NavLink>
            </Breadcrumb.Item>
        );
    });

    //    ""
    const breadcrumbItems = [
        <Breadcrumb.Item key="home">
            <NavLink to="/"></NavLink>
        </Breadcrumb.Item>,
        ...extraBreadcrumbItems,
    ];

    const handleLogout = () => {
        clearSession();
        navigate("/login");
    };

    return (
        <Layout style={{ minHeight: "100vh" }}>
            <Sider collapsible>
                {/*    */}
                <Logo />
                <Menu theme="dark" mode="inline" selectedKeys={[selectedKey]}>
                    <Menu.Item key="/" icon={<HomeOutlined />}>
                        <NavLink to="/"></NavLink>
                    </Menu.Item>
                    <Menu.Item key="/courses" icon={<BookOutlined />}>
                        <NavLink to="/courses"></NavLink>
                    </Menu.Item>
                    <Menu.Item key="/schedule" icon={<CalendarOutlined />}>
                        <NavLink to="/schedule"></NavLink>
                    </Menu.Item>
                    <Menu.Item key="/profile" icon={<UserOutlined />}>
                        <NavLink to="/profile"></NavLink>
                    </Menu.Item>
                    {user?.role === "teacher" && (
                        <Menu.Item key="/teacher-zone" icon={<PieChartOutlined />}>
                            <NavLink to="/teacher-zone"> </NavLink>
                        </Menu.Item>
                    )}
                </Menu>
            </Sider>

            <Layout className="site-layout">
                {/*  */}
                <Header style={{
                    background: "#fff",
                    padding: "0 16px",
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center"
                }}>
                    {/*    */}
                    <Breadcrumb>{breadcrumbItems}</Breadcrumb>

                    {/*   + Logout  */}
                    <div>
                        {user?.fullName} ({user?.role}) |{" "}
                        <span style={{ cursor: "pointer", color: "#1890ff" }} onClick={handleLogout}>
              
            </span>
                    </div>
                </Header>

                {/*  */}
                <Content style={{ margin: "16px" }}>
                    <Outlet />
                </Content>
            </Layout>
        </Layout>
    );
};
